#form-component {

  #form-head {
    background-color: $background-color;
    position: sticky;
    top: $header-height;
    z-index: 5;
    padding-top: $spacing;
    padding-bottom: 8px;

    .base-row-parent {
      border-bottom: $separation-line;
      position: relative;
      cursor: pointer;

      &:hover {
        color: $app-color;
      }

      &::after {
        content: '';
        position:absolute;
        height: 100%;
        width: 100%;
        z-index: 10;
        top: 0;
        left: 0;
        background-color: rgb(240, 240, 240);
        opacity: 0.5;
      }

      &:hover::after {
        opacity: 0;
      }
    }
  }
}

.options-button-row {
  width: 100%;

  .options {
    margin: auto;
  }
}

.slide-fade2-enter-active, .slide-fade2-move {
  transition: all 0.5s ease;
}
.slide-fade2-enter, .slide-fade2-leave-to {
  opacity: 0;
  transform: translateY(-#{$spacing});
}

.slide-fade2-leave-active {
  position: absolute;
  transition: all 0.3s ease;
}

.file-boxes {
  display: flex;

  div:not(:last-child) {
    margin-right: 16px;
  }
}

.file-list {
  margin-top: $spacing;
}

#menu-sidebar + #form-component {
  margin-left: 16px;
}

#form-save-button {
  border-left: $separation-line;
}

.subtitle {
  color: $font-color-second;
  height: $row-height-small;
  line-height: $row-height-small;
  margin: $spacing-small $spacing;
}

.form-button-child {
  display: block;
  border-left: $separation-line;
}

.popup-text {
  display: flex;
  align-items: flex-end;
}

.popup-text > div:first-of-type {
  margin-right: 16px;
}

.files-popup-input-field {
  margin-bottom: $spacing;
}

.popup-upload-area {
  margin: $spacing 0;
}

@media screen and (max-width: $mobile) {
  #form-component {
    max-width: 100%;
  }

  #menu-sidebar + #form-component {
    margin-left: 0;
  }

  #form-back-button, #form-save-button {
    width: 50%;
  }

  #form-back-button {
    order: 1;
  }

  #form-save-button {
    order: 2;
  }

  .form-button-inner {
    width: 100%;
  }
}
