




























@import "./styles/variables.scss";

.wrapper {
  display: block;
  margin: 0 auto;
  max-width: $page-max-width;
  min-width: $page-min-width;
  padding: $spacing;
  position: relative;
  min-height: 100vh;

  #app-container {
    margin-top: $header-height;
    display: flex;

    .sidebar {
      flex: 1 0 33%;
      max-width: 33%;
      align-self: flex-start;
      position: sticky;
      top: $header-height;
      padding-top: $spacing;
    }

    & .sidebar-full {
      max-width: 100%;
    }

    .form-view {
      flex: 0 1 66%;
      margin-left: 16px;
      max-width: 66%;
    }
  }
}

.mobile-elements {
  display: none;
}

#menu-sidebar + #form-component {
  margin-left: 0;
}

.options-row {
  min-height: $row-height-small;
  width: 100%;
  display: flex;
  background-color: $background-color;

  &.flex-align-right {
    justify-content: flex-end;
    margin-right: -32px;
  }

  .options {
    flex-grow: 2;
  }
}

.base-row {
  height: $row-height-large;
  box-shadow: $box-shadow-reg;
  display: flex;
  margin-bottom: $spacing-small;
  width: 100%;

  &:hover {
    box-shadow: $box-shadow-hov;
  }

  .base-row-header {
    overflow: hidden;
    width: 100%;
  }
}

.options-button-row {
  width: 100%;

  .options {
    margin: auto;
  }
}

.base-row {
  height: $row-height-large;
  box-shadow: $box-shadow-reg;
  display: flex;
  margin-bottom: $spacing-small;
  width: 100%;

  &:hover {
    box-shadow: $box-shadow-hov;
  }

  .base-row-header {
    overflow: hidden;
    width: 100%;
  }
}

@media screen and (max-width: $mobile) {
  .wrapper {
    padding: $spacing-small;
  }

  .mobile-hidden {
    display: none;
  }

  .mobile-elements {
    display: block;
  }

  .options-row {
    flex-wrap: wrap;
    height: auto;
  }

  .options-row.flex-align-right {
    justify-content: center;
  }

  .base-row .base-row-header {
    order: 3;
    border-top: $separation-line;
  }

  .base-row {
    height: auto;
    flex-wrap: wrap;
  }
}
