.base-form {
  background-color: white;
  display: flex;
  flex-wrap: wrap;
  padding: 16px;
}

.base-form-field:not(:last-of-type) {
  margin-bottom: $spacing;
}

.base-form-field-full {
  flex: 0 0 100%;
}

.base-form-field-half {
  flex: 0 0 calc(50% - 8px);
}

.base-form-field-spacing {
  margin-left: 16px;
}

@media screen and (max-width: $mobile) {
  .base-form-field-half {
    flex: 0 0 100%;
  }

  .base-form-field-spacing {
    margin-left: 0;
  }

  .multiline-dropdown {
    order: 0;
  }
}
